<template>
  <div :title="code">
    <el-icon class="state-icon-ok" v-show="statetype == 'success'"
      ><SuccessFilled
    /></el-icon>
    <span class="state-text"> {{ stateText }} </span>
    <el-icon class="state-icon-warn" v-show="statetype == 'warn'"
      ><WarnTriangleFilled
    /></el-icon>
    <el-icon class="state-icon-error" v-show="statetype == 'error'"
      ><CircleCloseFilled
    /></el-icon>
  </div>
</template>

<script setup>
import {
  SuccessFilled,
  WarnTriangleFilled,
  CircleCloseFilled,
} from "@element-plus/icons-vue";
</script>

<script>
const StateTextMap = {
  ready: { type: "success", text: "正常" },
  moved: { type: "warn", text: "已搬迁" },
  offline: { type: "error", text: "找不到" },
};

export default {
  name: "file-state",

  computed: {
    stateText() {
      return this.stringifyState(this.code);
    },

    statetype() {
      return this.getStateType(this.code);
    },
  },

  data() {
    return {
      // stateText: "(ST)",
    };
  },

  methods: {
    stringifyState(code) {
      let info = StateTextMap[code];
      if (info == null) {
        return code;
      }
      return info.text;
    },

    getStateType(code) {
      let info = StateTextMap[code];
      if (info == null) {
        return code;
      }
      return info.type;
    },
  },

  props: {
    code: String,
  },
};
</script>

<style scoped>
.state-icon-ok {
  color: green;
}

.state-icon-warn {
  color: rgb(255, 208, 0);
}

.state-icon-error {
  color: red;
}

.state-text {
  margin-left: 5px;
  margin-right: 5px;
}
</style>
